:root {
  font-family: "Commissioner", sans-serif;
}

:root {
  --light-cyan: hsl(176, 50%, 47%);
  --moderate-cyan: #3cb4ac;
  --dark-cyan: #147b74;
  --black: hsl(0, 0%, 0%);
  --dark-gray: hsl(0, 0%, 48%);
  --light-gray: hsl(0, 0%, 98%);
  --bookmarkBtn-background: hsl(0, 0%, 96%);
  --bookmarkBtn-oval-hover: #707070;
  --btn-hover-background: #147a73;
}

:root {
  --card-radius: 0.5rem;
  --card-border: 1px solid rgba(0, 0, 0, 0.15);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

*:focus {
  outline: none;
  border: 2px dashed #c19a6b;
}

body {
  margin: 0;
  padding: 0;
  caret-color: var(--moderate-cyan);
}

h1 {
  color: black;
  font-style: normal;
  font-weight: 700;
  font-size: 1.375rem;
  line-height: 1.2;
}
@media (min-width: 40em) {
  h1 {
    font-size: 1.25rem;
  }
}

h2 {
  color: black;
  font-style: normal;
  font-weight: bold;
  font-size: 1.125rem;
  line-height: 1.2;
}
@media (min-width: 40em) {
  h2 {
    font-size: 1.25rem;
  }
}

h3 {
  color: black;
  font-style: normal;
  font-weight: bold;
  font-size: 0.875rem;
  line-height: 1.2;
}
@media (min-width: 40em) {
  h3 {
    font-size: 1.125rem;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  object-fit: contain;
}

.large {
  display: none !important;
}

.med-desk {
  display: none !important;
}

@media (min-width: 40em) {
  .med-desk {
    display: initial !important;
  }

  .mobile {
    display: none !important;
  }
}
@media (min-width: 53.75em) {
  .large {
    display: initial;
  }

  .medium {
    display: none;
  }
}
h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

body {
  background-color: var(--light-gray);
  padding-bottom: 4.75rem;
}
@media (min-width: 53.75em) {
  body {
    padding-bottom: 7.75rem;
  }
}

.container {
  padding: 0 1.5rem;
  margin: 0 auto;
  width: 100%;
}
@media (min-width: 40em) {
  .container {
    padding: 3rem;
  }
}
@media (min-width: 53.75em) {
  .container {
    padding: 0;
    width: 77%;
    max-width: 69.375rem;
  }
}

.header {
  min-height: 18.75rem;
  background: url(../images/image-hero-mobile.jpg) no-repeat;
  background-size: cover;
}
.header__logo {
  z-index: 3;
  cursor: pointer;
}
.header__gradient {
  width: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.0001) 100%);
  mix-blend-mode: normal;
  height: 40%;
}
.header .navbar {
  z-index: 2;
  padding-top: 2rem;
  padding-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .nav {
  position: absolute;
  right: 0;
  top: -100%;
  width: 100%;
  height: 100%;
  z-index: 2;
  padding: 0 1.5rem;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.0001) 100%);
  mix-blend-mode: normal;
  transition: all ease-in-out 0.3s;
}
.header .nav.open {
  top: 0;
}
.header .nav__list {
  list-style: none;
  width: 100%;
  margin-top: 5.5rem;
  background: white;
  padding: 0;
  border-radius: 0.5rem;
}
.header .nav__list .navlink:not(:last-child) {
  border-bottom: 1px solid rgba(47, 47, 47, 0.1);
}
.header .nav__list .navlink a {
  text-decoration: none;
  color: #000;
  display: inline-block;
  width: 100%;
  padding: 1.5rem;
  font-style: normal;
  font-weight: 500;
  font-size: 1.125rem;
  line-height: 1.2;
}
.header__menuControls {
  z-index: 3;
  cursor: pointer;
}
.header__menuControls .closeMenu {
  display: none;
}
.header__menuControls .burger {
  display: inline-block;
}
.header__menuControls.open .closeMenu {
  display: inline-block;
}
.header__menuControls.open .burger {
  display: none;
}
@media (min-width: 40em) {
  .header {
    background: url(../images/image-hero-desktop.jpg) center no-repeat;
    min-height: 25rem;
  }
  .header__menuControls {
    display: none;
  }
  .header .navbar {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
  .header .nav {
    position: static;
    padding: 0;
    background: none;
    width: initial;
  }
  .header .nav__list {
    width: initial;
    margin: 0;
    background: none;
    border-radius: 0;
    display: flex;
    align-items: center;
    gap: 2rem;
  }
  .header .nav__list .navlink {
    border: none;
  }
  .header .nav__list .navlink a {
    color: white;
    font-size: 0.8125rem;
    line-height: 1rem;
    padding: 0.1875rem;
  }
  .header .nav__list .navlink a:hover, .header .nav__list .navlink a:focus {
    text-decoration: underline;
  }
}

.card {
  width: 100%;
  max-width: 28.125rem;
  margin: 0 auto 1.5rem;
  background-color: white;
  border-radius: var(--card-radius);
  padding: 2.5rem 1.5rem;
  font-size: 0.875rem;
  line-height: 1.7;
  color: var(--dark-gray);
}
.card:last-child {
  margin-bottom: 0;
}
.card header {
  margin-bottom: 1.5rem;
}
.card p:not(:last-child) {
  margin-bottom: 1.5rem;
}
.card__heading {
  color: black;
  font-weight: 700;
}
.card__subHeading {
  color: var(--light-cyan);
  font-weight: 500;
}
@media (min-width: 40em) {
  .card {
    max-width: 45.625rem;
    font-size: 1rem;
    width: 90%;
    padding: 3rem;
  }
}

.introCard {
  text-align: center;
  margin-top: -3.4375rem;
  padding-top: 3.25rem;
  position: relative;
}
.introCard .mastercraft-logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}
.introCard h1 {
  margin: 0 auto 1rem;
}
.introCard .description {
  margin-bottom: 1.5rem;
}
.introCard .button-cont {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 40em) {
  .introCard {
    margin-top: -5.75rem;
    padding-top: 3.5rem;
  }
  .introCard h1 {
    font-size: 1.75rem;
  }
  .introCard .description {
    margin-bottom: 2.5rem;
  }
}

button,
.btn {
  font-size: 1rem;
  padding: 1.125em 2.625em;
  border-radius: 6.25rem;
  background-color: var(--moderate-cyan);
  color: white;
  font-style: normal;
  font-weight: bold;
  line-height: 1.25;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  cursor: pointer;
}
button.smallBtn,
.btn.smallBtn {
  font-size: 0.875rem;
}
button:disabled,
.btn:disabled {
  background: #2f2f2f;
  opacity: 0.5;
  cursor: not-allowed;
}
button:disabled:focus, button:disabled:hover,
.btn:disabled:focus,
.btn:disabled:hover {
  background: #2f2f2f;
}
button:focus, button:hover,
.btn:focus,
.btn:hover {
  background-color: var(--btn-hover-background);
}

.bookmarkBtn {
  padding: 0;
  border: none;
  outline: none;
  color: var(--dark-gray);
  background-color: var(--bookmarkBtn-background);
}
.bookmarkBtn span {
  padding: 1.125em 1.5em 1.125em 1em;
}
.bookmarkBtn:hover, .bookmarkBtn:focus {
  background-color: var(--bookmarkBtn-background);
}
.bookmarkBtn:hover svg circle, .bookmarkBtn:focus svg circle {
  fill: var(--bookmarkBtn-oval-hover);
}
.bookmarkBtn.bookmarked {
  color: var(--btn-hover-background);
}
.bookmarkBtn.bookmarked svg path {
  fill: white;
}
.bookmarkBtn.bookmarked svg circle {
  fill: var(--btn-hover-background);
}

.analyticsCard .analytics {
  list-style: none;
  text-align: center;
}
.analyticsCard .analytics li:first-child {
  padding-top: 0;
}
.analyticsCard .analytics li:nth-child(2) {
  position: relative;
}
.analyticsCard .analytics li:nth-child(2)::before, .analyticsCard .analytics li:nth-child(2)::after {
  content: " ";
  display: inline-block;
  width: 5rem;
  height: 1px;
  position: absolute;
  transform: translateX(-50%);
  background-color: #000;
  opacity: 0.15;
  margin: 0 auto;
}
.analyticsCard .analytics p.analytics__figure {
  padding-top: 1.5rem;
  margin-bottom: 0.5625rem;
  font-style: normal;
  font-weight: bold;
  font-size: 2rem;
  line-height: 1.2;
  color: black;
}
.analyticsCard .analytics__detail {
  padding-bottom: 1.5rem;
  line-height: 1.2;
}
.analyticsCard progress {
  /* Reset the default appearance */
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  height: 0.75rem;
  margin-top: 0.5rem;
}
.analyticsCard progress::-webkit-progress-bar {
  background-color: var(--light-gray);
  border-radius: 100px;
}
.analyticsCard progress::-webkit-progress-value {
  background-color: var(--moderate-cyan);
  border-radius: 100px;
}
@media (min-width: 53.75em) {
  .analyticsCard .analytics {
    display: flex;
    align-items: center;
    margin-bottom: 1.75rem;
  }
  .analyticsCard .analytics li {
    flex: 1;
    text-align: left;
  }
  .analyticsCard .analytics li:not(:first-child) {
    padding-left: 2.9375rem;
  }
  .analyticsCard .analytics li:nth-child(2)::before {
    height: 3.9375rem;
    width: 1px;
    left: 0;
    top: 50%;
    transform: translate(0, -50%);
  }
  .analyticsCard .analytics li:nth-child(2)::after {
    height: 3.9375rem;
    width: 1px;
    right: 0;
    top: 50%;
    transform: translate(0, -50%);
  }
  .analyticsCard .analytics__figure {
    padding-top: 0;
  }
  .analyticsCard .analytics__detail {
    padding-bottom: 0;
  }
}

.aboutCard .about {
  margin-bottom: 2.1875rem;
}
.aboutCard .productList {
  list-style: none;
}
.aboutCard .productList li:not(:last-child) {
  margin-bottom: 1.5rem;
}
@media (min-width: 40em) {
  .aboutCard header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.productCard {
  border: var(--card-border);
  padding: 1.5rem;
}
.productCard.out-of-stock {
  opacity: 0.5;
}
.productCard-name:not(:last-child) {
  margin-bottom: 0.5rem;
}
.productCard-amount {
  font-size: 0.9375rem;
  display: flex;
  align-items: center;
}
.productCard-amount span {
  font-weight: 700;
  font-size: 2rem;
  line-height: 1.2;
  color: black;
  margin-right: 0.5rem;
}
@media (min-width: 40em) {
  .productCard-name p {
    font-size: 0.9375rem;
  }
  .productCard-actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.body-blackout {
  position: absolute;
  z-index: 1010;
  left: 0;
  top: 0;
  width: 100%;
  height: 1800px;
  background-color: rgba(0, 0, 0, 0.65);
  display: none;
}
.body-blackout.is-blacked-out {
  display: block;
}

.modal {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 7.5625rem;
  margin-bottom: 7.5625rem;
  width: calc(100% - 3rem);
  z-index: 1011;
  opacity: 0;
  pointer-events: none;
  transition: all 300ms ease-in-out;
}
.modal.is--visible {
  opacity: 1;
  pointer-events: auto;
}
.modal__close {
  cursor: pointer;
}

.popup-modal > header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.popup-modal ul {
  list-style: none;
}
.popup-modal ul li:not(:last-child) {
  margin-bottom: 1.5rem;
}
.popup-modal .cardWrap {
  border: var(--card-border);
  border-radius: var(--card-radius);
}
.popup-modal .cardWrap .productCard {
  margin-bottom: 0;
  border: none;
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-template-rows: auto;
  grid-template-areas: "radio header amount" "description description description";
  align-items: center;
  row-gap: 1.5rem;
  column-gap: 1rem;
}
.popup-modal .cardWrap .productCard .radio {
  grid-area: radio;
}
.popup-modal .cardWrap .productCard .head {
  gap: 1rem;
  grid-area: header;
  margin-bottom: 0;
}
.popup-modal .cardWrap .productCard .head:hover h3 {
  color: var(--moderate-cyan);
}
.popup-modal .cardWrap .productCard .head:hover ~ .radio > span {
  border-color: var(--moderate-cyan);
}
.popup-modal .cardWrap .productCard .head h3 {
  display: inline-block;
  cursor: pointer;
}
.popup-modal .cardWrap .productCard .amount {
  grid-area: amount;
}
.popup-modal .cardWrap .productCard .description {
  grid-area: description;
}
.popup-modal .cardWrap .productForm {
  text-align: center;
  border-top: var(--card-border);
  padding: 1.5rem 1rem;
  display: none;
}
.popup-modal .cardWrap .productForm p {
  margin-bottom: 1rem;
}
.popup-modal .cardWrap .productForm form {
  display: grid;
  align-items: center;
  grid-template-columns: auto;
  grid-template-rows: auto;
  grid-template-areas: "input button";
}
.popup-modal .cardWrap .productForm form .errorMessage {
  display: none;
  grid-area: error;
  color: red;
  margin-bottom: 0.375rem;
  line-height: 1;
}
.popup-modal .cardWrap .productForm form input[type=text] {
  grid-area: input;
}
.popup-modal .cardWrap .productForm form input[type=submit] {
  grid-area: button;
}
.popup-modal .cardWrap .productForm form.error {
  grid-template-areas: "error error" "input button";
}
.popup-modal .cardWrap .productForm form.error .errorMessage {
  display: block;
}
.popup-modal .cardWrap .productForm form.error input[type=text] {
  border-color: red;
}
.popup-modal .cardWrap .productForm form.error input[type=submit] {
  background: #2f2f2f;
  opacity: 0.5;
  cursor: not-allowed;
}
.popup-modal .cardWrap .productForm form.error input[type=submit]:focus, .popup-modal .cardWrap .productForm form.error input[type=submit]:hover {
  background: #2f2f2f;
}
@media (min-width: 40em) {
  .popup-modal .cardWrap .productForm {
    padding: 1.5rem;
  }
}
.popup-modal .cardWrap.checked {
  border-color: var(--moderate-cyan);
  border-width: 2px;
}
.popup-modal .cardWrap.checked .productForm {
  display: block;
}
.popup-modal .cardWrap.out-of-stock {
  pointer-events: none;
  opacity: 0.5;
}
@media (min-width: 40em) {
  .popup-modal {
    width: calc(90% - 6rem);
  }
}
@media (min-width: 40em) {
  .popup-modal {
    width: 69.3%;
  }
}

.success-modal {
  margin-top: 8.9375rem;
  margin-bottom: 8.9375rem;
}

.input-wrap {
  display: block;
  position: relative;
}
.input-wrap .prefix {
  position: absolute;
  top: 50%;
  padding: 0rem 0.8rem;
  padding-left: 1.5rem;
  transform: translateY(-50%);
  font-style: normal;
  font-weight: 700;
  font-size: 0.875rem;
}

input[type=text] {
  padding: 1rem 1.375rem;
  padding-left: 2.5rem;
  outline: none;
  border: var(--card-border);
  border-radius: 100px;
  width: 6.25rem;
  font-size: 0.875rem;
  text-align: center;
  color: black;
  font-weight: 700;
  line-height: 1.2;
  margin-right: 1rem;
  font-style: normal;
}
input[type=text]:hover, input[type=text]:focus {
  border-color: var(--moderate-cyan);
}

.radio {
  display: flex;
  position: relative;
}
.radio input {
  position: absolute;
  opacity: 0;
  width: 100%;
  height: 100%;
}
.radio span {
  display: block;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  border: var(--card-border);
  display: grid;
  place-items: center;
}
.radio span::before {
  content: "";
  width: 0.75rem;
  height: 0.75rem;
  background-color: var(--moderate-cyan);
  border-radius: 50%;
  transition: 180ms transform ease-in-out;
  transform: scale(0);
}
.radio input:checked + span {
  border-color: var(--moderate-cyan);
}
.radio input:checked + span::before {
  transform: scale(1);
}
.radio input:focus + span {
  box-shadow: 0 0 0 0.05em #fff, 0 0 0.15em 0.1em var(--moderate-cyan);
  border-color: var(--moderate-cyan);
}
.radio input:hover + span {
  border-color: var(--moderate-cyan);
}